@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200&display=swap');
body {
  margin: 0;
  font-family: 'Noto Sans', sans-serif, 'Noto Sans JP';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0 auto;
  background: black;
  background-attachment:fixed;
  color: white;
  margin-top: 50px;
}

.Generate{

}

.sidemenu{
  position: fixed;
  top: 0;
  left: 0;
  width: 300px;
  z-index: 1;
  height: 100%;
  background-color: black;
  padding-top: 50px;
  overflow: scroll;
}


.sidemenuItem{
  color: #7C7C7C;
}

.sidemenuItem a{
  color: #7C7C7C;
  text-decoration: none;
}

.sidemenuItem p{
  font-size: 10px;
}

.sidemenuItemHome,.sidemenuItemAbout, .sidemenuItemArtist{
  display: flex;
  align-items: center;
  margin-left: 10px;
  gap: 10px;
  margin-top: -10px;
}

.headermenu{
  color: white;
  position: fixed;
  top: 0;
  display: flex;
  justify-content: flex-end;
  gap: 20px;
  align-items: center;
  background-color: black;
  height: 70px;
  width: 100%;
}


.login{
  background-color: #FFE300;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  margin: 0 auto;
  margin-top: 50px;
  border-radius: 20px;
  color: black;
  font-weight: bold;
  height: 50px;
}

.signup{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  margin: 0 auto;
  font-weight: bold;
}

.loginButton{
}

.linkToStr{
  background-color: #292929;
  margin-left: 20px;
  padding: 10px 10px 10px 10px;
  border-radius: 5px;
  margin: 0 auto;
  width: 85%;
  line-height: 15px;
}

.linkToStr p{
  font-size: 11px;
  margin-bottom: 20px;
}

.linkToStrButton{
  color: black;
  background-color: white;
  text-decoration: none;
  text-align: center;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  width: 100%;
  transition: 0.4s;
}


.linkToStrButton:hover{
  opacity: 0.5;
}

.linkToStrButton a{
  text-decoration: none;
  padding: 10px;
  font-size: 12px;
  color: black;
  font-weight: bold;
}

.header{
  color: aliceblue;
  text-align: center;
  margin: 0;
  padding: 10px;
  font-size: 30px;
  font-family: 'Josefin Sans', sans-serif;
}

.formLabel{
  display: flex;
  width: 300px;
  margin: 0 auto;
  color: white;
  font-size: 14px;
}

.formItem{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px auto 50px auto;
}


.formItem input{
  border: none;
  height: 30px;
  padding: 10px;
  width: 300px;
  font-size: 20px;

  border-radius: 30px;
}

.formItem input:focus{
  outline: none;
  background-color: azure;
  opacity: 0.5;
}

.formItem button{
  width: 200px;
  margin: 60px auto;
  font-size: 20px;
  padding: 20px;
  background-color:rgba(0, 102, 255,0.5);
  border-radius: 20px;
  cursor: pointer;
  color: white;
  border: none;
}

.loading{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.footer{
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 10;
  background-color: #FFE300;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  font-family: sans-serif;
}

.footerCaption{
  color: black;
  padding: 0px 20px;
  line-height: 13px;
}

.footerCaption h3{
  font-size: 18px;
}

.footerCaption p{
  font-size: 11px;
}

.footerButton{
  margin-right: 30px;
  color: black;
}

.footerButton p{
  padding: 10px 40px;
  border-radius: 10px;
  background-color: white;
  font-family: sans-serif;
  font-weight: bold;
  font-size: 14px;
}

.playlist{
  margin-left: 320px;
  margin-bottom:100px;
  margin-top: 70px;
  padding: 10px 20px 10px 50px;
  background: linear-gradient(#292929, 30%, black);
}

.playlist a{
  text-decoration: none;
  color: white;
}

.playlist h2{
  color: white;
}

.playlist1, .playlist2, .playlist3 {
  display: flex;
  gap: 20px;
  cursor: pointer;
  overflow: scroll;
}

.tune{
  padding: 15px;
  background-color:rgba(0,0,0,0.3);
  border-radius: 5px;
  width: 200px;
  height: 350px;
  white-space: pre-wrap;
}

.tune:hover{
  opacity: 0.5;
}

.tuneTitle{
  font-size: 15px;
  font-weight: bold;
  margin-bottom: -10px;
}

.tuneCaption{
  font-size: 10px;
  margin-top:50px;
}

.playlist img{
  width: 200px;
  height: 200px;
}

.playlist3 img{
  width: 200px;
  height: 200px;
}

.modal{
  position: absolute;
  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  transform: translate(-50%, -50%);
  width:500px;
  height: 500px;
  border: none;
  background-color: white;
  text-align: center;
  border-radius: 5px;
  outline: none;
  color:black;
}

.modal img{
  width: 300px;
  margin-top:10px;
}

.rhap_container{
  position: absolute;
  bottom:0;
}

.artistHome{
  margin-left: 300px;
  margin-bottom: 300px;
}

.artistHome header{
  height: 400px;
}

.artistProfile{
  margin-top: -170px;
  background-color: #7C7C7C;
  padding: 5px 30px 5px 30px;
  color: black;
  white-space: pre-wrap;
}

.artistProfile h3{
  font-weight:bold;
  margin-bottom: -10px;
}

.promptheader{
 font-size: 20px;
}

.artistHomeSubheader{
  font-family: 'Josefin Sans', sans-serif;
  margin-left: 50px;
  font-size: 20px;
}

.artistHeader{
  font-family: 'Josefin Sans', sans-serif;
  margin-top: 150px;
  margin-left: 50px;
}

.artistHeader h1{
  font-size: 70px;
}

.artistHeader p{
  font-size: 40px;
  margin-bottom: -50px;
}

.artistHeader button{
  border: 1px solid #FFE300;
  background-color: white;
  color: black;
  font-weight: bold;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
  margin-top: -20px;
  width: 100px;
}



.artistHomeHeaderImage img{
  position: absolute;
  width: 100%;
  top:0;
  left: 0;
  z-index: -100;
  opacity: 1;
  height: 400px;
  object-fit:cover;
}
.artistHomeTune{
  display: flex;
  gap: 30px;
  justify-content: center;
  align-items: center;
  margin-bottom: 1px;
  padding: 50px;
  background-color: #222222;
  border-radius: 25px;
}
.artistHomeTune img{
  width: 150px;
  height: 150px;
}
.musicTitle{
  width:300px;
}
.playButton{
  cursor: pointer;
  font-size: 50px;
}

.playButton:hover{
  opacity: 0.3;
}

.about{
  margin-left: 300px;
  margin-top: 70px;
  background: linear-gradient(#292929, 30%, black);
  padding: 10px 20px 10px 50px;
  height: 100%;
  margin-bottom: 300px;
  font-size: 15px;
}

@media screen and (max-width:767px) {
  .playlist img{
    width: 100px;
    height: 100px;
  }
  .tuneTitle{
    font-size: 14px;
  }
  .artistHeader{
    margin-top: 200px;
    margin-left: 20px;
  }
  .artistHeader h1{
    font-size: 50px;
  }
  .artistHeader p{
    font-size: 30px;
    margin-bottom: -20px;
  }
  .artistHeader button{
    padding: 10px;
    margin: 5px;
    border-radius: 5px;
    margin-top: -20px;
    width: 70px;
    font-size: 12px;
  }
  .artistHomeSubheader{
    margin-top: -150px;
  }
  .artistHomeTune img{
    width: 100px;
    height: 100px;
  }
}
